<template>
  <el-container>
    <Header title="写与编辑"/>
    <el-main>
      <el-input size="small" class="ara-title" v-model="title" placeholder="标题">
        <el-select slot="prepend" class="ara-title-type" v-model="type">
          <el-option value="1" label="设定">设定</el-option>
          <el-option value="2" label="正章">正章</el-option>
          <el-option value="3" label="人物">人物</el-option>
          <el-option value="4" label="术式">术式</el-option>
        </el-select>
      </el-input>
      <article class="ara-main">
        <header class="ara-tools-parent">
          <el-button-group class="cm-horizon ara-tools-cont">
            <el-button type="primary" @click="tools('p')">P</el-button>
            <el-button type="primary" @click="tools('link')" icon="el-icon-connection"></el-button>
            <el-button type="primary" @click="tools('h1')">h1</el-button>
            <el-button type="primary" @click="tools('h2')">h2</el-button>
            <el-button type="primary" @click="tools('h3')">h3</el-button>
            <el-button type="primary" @click="tools('h4')">h4</el-button>
          </el-button-group>
          <el-button-group class="cm-horizon ara-tools-cont ara-tools-cont2">
            <el-button type="primary" @click="tools('ul')">ul</el-button>
            <el-button type="primary" @click="tools('ol')">ol</el-button>
          </el-button-group>
        </header>
        <main>
          <el-input ref="refContent" v-model="content" resize="none" type="textarea" :show-word-limit="true"
                    class="ara-textarea">
          </el-input>
          <div class="ara-pic-upload cm-padding-v">
            <el-upload
              action="#"
              class="ara-pic-upload-cont cm-horizon"
              list-type="picture-card"
              :auto-upload="false">
              <i slot="default" class="el-icon-plus"></i>
              <div slot="file" slot-scope="{file}">
                <img
                  class="el-upload-list__item-thumbnail"
                  :src="file.url" alt=""
                />
                <span class="el-upload-list__item-actions">
                  <span
                    class="el-upload-list__item-preview"
                    @click="handlePictureCardPreview(file)"
                  >
                    <i class="el-icon-zoom-in"></i>
                  </span>
                  <span
                    v-if="!disabled"
                    class="el-upload-list__item-delete"
                    @click="handleDownload(file)"
                  >
                    <i class="el-icon-download"></i>
                  </span>
                  <span
                    v-if="!disabled"
                    class="el-upload-list__item-delete"
                    @click="handleRemove(file)"
                  >
                    <i class="el-icon-delete"></i>
                  </span>
                </span>
              </div>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
          </div>
        </main>
      </article>
    </el-main>
    <footer class="ara-footer">
      <el-button type="success" @click="submit">提交</el-button>
    </footer>
  </el-container>
</template>

<script>
  import {AV} from '../../common/api';
  import Model from '../../model';
  export default {
    computed: {},
    methods: {
      submit(){
        const {title, content, type, objectId} = this;
        if (title && content) {
          const loading = this.$loading(this.$loadingConfig)
          if (objectId) {
            AV.updateData({tableName: 'articleList', objectId, data: {type: Number(type), title, content}}).then(() => {
              this.$alert('提交成功');
              this.$linkTo(`/ArticleDetail/${objectId}`);
            }).catch(e => {
              this.$alert('提交失败,' + e);
            }).finally(() => {
              loading.close()
            })
          } else {
            AV.addData('articleList', {type: Number(type), title, content}).then((res) => {
              this.objectId = res.objectId;
              this.$alert('提交成功');
              this.$linkTo(`/ArticleDetail/${this.objectId}`);
            }).catch(e => {
              this.$alert('提交失败,' + e);
            }).finally(() => {
              loading.close()
            })
          }
        } else {
          this.$alert('标题和内容不可为空!');
        }
      },
      tools(type){
        let html = Model.articleTag[type];

        const selectedText = this.getSelectionText();
        const dom = this.$refs.refContent.$el;
        const textArea = dom.getElementsByClassName("el-textarea__inner")[0];
        const cursorPosition = this.getCursorPosition(textArea);
        let sliceEnd = cursorPosition;
        let str = this.content;
        if (selectedText) {
          sliceEnd = cursorPosition + selectedText.length;
          html = html.replace('{replace}', selectedText);
        } else {
          html = html.replace('{replace}', '');
        }
        this.content = str.slice(0, cursorPosition) + html + str.slice(sliceEnd);
      },
      getSelectionText(){
        let str = '';
        try {
          const selectedText = window.getSelection().toString();
          if (selectedText && selectedText.trim() !== "") {
            str = selectedText;
          }
        } catch (err) {
          const selectedText = document.selection.createRange();
          str = selectedText.text;
        }
        return str;
      },
      getCursorPosition(obj) {
        let cursorIndex = 0;
        if (document.selection) {
          // IE Support
          obj.focus();
          const range = document.selection.createRange();
          range.moveStart('character', -obj.value.length);
          cursorIndex = range.text.length;
        } else if (obj.selectionStart || obj.selectionStart === 0) {
          // another support
          cursorIndex = obj.selectionStart;
        }
        return cursorIndex;
      },
      getArticleDetail(){
        if (this.objectId) {
          const loading = this.$loading(this.$loadingConfig)
          AV.getData('articleList', this.objectId).then(res => {
            this.title = res.title;
            this.content = res.content;
          }).catch(e => {
            this.$alert('获取文章失败,' + e);
          }).finally(() => {
            loading.close()
          });
        }
      },
      handleRemove(file) {
        console.log(file);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      handleDownload(file) {
        console.log(file);
      }
    },
    data(){
      const {type, objectId} = this.$route.params;
      return {
        type,
        objectId,
        content: '',
        title: '',
        dialogImageUrl: '',
        dialogVisible: false,
        disabled: false
      }
    },
    created(){

    },
    mounted(){
      this.getArticleDetail();
    },
  }
</script>

<style>
  .ara-main {
    margin-top: var(--size-md);
  }

  .ara-title {

  }

  .ara-title-type {
    width: 100px;
  }

  .ara-tools-parent {
    margin-bottom: -1px;
  }

  .ara-tools-cont {
    width: 100%;
  }

  .ara-tools-cont > .el-button {
    flex: 1;
    padding-left: 0;
    padding-right: 0;
  }

  .ara-tools-cont > .el-button:last-child {
    border-bottom-right-radius: 0;
  }

  .ara-tools-cont > .el-button:first-child {
    border-bottom-left-radius: 0;
  }

  .ara-tools-cont2 {
    margin-top: 1px;
  }

  .ara-tools-cont2 > .el-button:last-child {
    border-top-right-radius: 0;
  }

  .ara-tools-cont2 > .el-button:first-child {
    border-top-left-radius: 0;
  }

  .ara-textarea > textarea {
    font-family: Constantia, 'Avenir', Helvetica, Arial, sans-serif;
    border-radius: 0 0 4px 4px;
    height: 300px;
  }

  .ara-pic-upload {

  }

  .ara-pic-upload .el-upload--picture-card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    line-height: 1;
  }

  .ara-pic-upload .el-upload-list--picture-card .el-upload-list__item {
    width: 70px;
    height: 70px;
  }

  .ara-pic-upload-button {
    width: 70px;
    height: 70px;
    border: dashed 1px var(--cor-grey);
    margin-top: 10px;
  }

  .ara-footer {

  }

  .ara-footer > .el-button {
    width: 100%;
    border-radius: 0;
  }
</style>



